import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IStockInicial } from '../../models/stock-inicial';
import { StockInicialService } from '../../service/stock-inicial.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { Observable, Subject, finalize, firstValueFrom, map, startWith, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { AddArticuloComponent } from '../addArticulo/add-articulo.component';
import { IAlertConfig } from '@shared/models/alert';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-save-stock-inicial',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    columns = ['codigointerno', 'articulo', 'unidadmedida', 'cantidad', 'acciones'];
    form: FormGroup;
    stock_inicial: IStockInicial;
    title: string;
    user: any;
    depositos: IDeposito[];
    articulos: IArticulo[];
    unidades_de_medida: any[] = [];
    stk_movimientos: any[] = [];
    mov_art_list_deleted: any[] = [];
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;
    filteredArticulos: Observable<IArticulo[]>;
    filteredDepositos: Observable<IDeposito[]>;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    deposito_default = 0;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _stockInicialService: StockInicialService,
        private _empresaService: EmpresaService,
        private datePipe: DatePipe,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>,
        private dialogo: MatDialog,

    ) {
        this.stock_inicial = this.data ? this.data : null;
        this.title = this.stock_inicial?.idstockinicial ? 'Editar stock inicial' : 'Nuevo stock inicial';
        /* this.user = JSON.parse(sessionStorage.getItem('user')) */
        this.user = JSON.parse(localStorage.getItem('user'))
        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
                this.deposito_default = empresa.deposito_default;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.exist();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    /**
    * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el límite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    /**
    * Si existe un stockinicial le mando 'true' a fondos y nada a TM para que me traiga todos los fondos y TMs al editar.
    * Caso contrario, si se va a crear un stockinicial, llamo a los fondos sin enviarle un parametro asi me trae solo los activos y
    * le envio un paramtro a los tipos de stockinicials para que me traiga los activos.
    */
    exist() {
        if (this.stock_inicial?.idstockinicial) {
            this.getDepositos(true);
        } else {
            this.getDepositos();
        }
    }


    private _filterDepositos(deposito: IDeposito): IDeposito[] {
        const filterValue = deposito.toString().toLowerCase();
        return this.depositos.filter(deposito => deposito.descripcion.toLowerCase().includes(filterValue));
    }

    getTextDeposito(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        return option;
    }


    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._stockInicialService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;

            // Filtrar y autoasignar el depósito por defecto
            const depositoPorDefecto = this.depositos.find(deposito => deposito.iddeposito === this.deposito_default);
            if (depositoPorDefecto && !this.stock_inicial) {
                this.form.get('deposito').setValue(depositoPorDefecto);
            }

            this.filteredDepositos = this.form.get('deposito').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterDepositos(value)),
            );

            resolve(true);
        })
    };

    async getMovimientos(idstockinicial): Promise<any> {
        return new Promise(async (resolve) => {
            const movimientos = await firstValueFrom(this._stockInicialService.getMovimientos(idstockinicial))
            this.stk_movimientos = movimientos.list;
            resolve(this.stk_movimientos);
        })
    }

    /**
     * Esta funcion añade un nuevo item a la grilla dentro del saveComponent.
     * @var art_descripcion busca en el arreglo correspondiente un elemento con el mismo id para imprimir su descripcion.
     * @var um_descripcion busca en el arreglo correspondiente un elemento con el mismo id para imprimir su descripcion.
     * @var art_stk_ini Encapsula un objeto con los datos que deseamos mostrar en la tabla.
     */
    addArticulo() {
        if (this.form.get('deposito').value != '') {
            let esEdicion = this.stock_inicial ? true : false;
            this.dialogo
                .open(AddArticuloComponent, {
                    panelClass: 'ayuda-dialog',
                    data: {
                        movimientos: this.stk_movimientos,
                        movimientos_eliminados: this.mov_art_list_deleted,
                        esEdicion: esEdicion
                    }
                })
                .afterClosed()
                .subscribe({
                    next: (res) => {
                        if (res) {
                            let art_stk_ini = {                                     //Encapsulo los datos que me interesan mostrar en la grilla.
                                idarticulo: res.articulo.idarticulo,
                                cantidad: res.cantidad,
                                idunidadmedida: res.unidad_medida,
                                descripcion_articulo: res.articulo.descripcion,
                                descripcion_um: res.unidadmedida_descripcion,
                                codigo_interno_articulo: res.articulo.codigo_interno
                            }
                            this.stk_movimientos = [art_stk_ini, ...this.stk_movimientos]; // Se agrega el nuevo articulo al comienzo de la lista para que siempre aparezca primero en la grilla
                            this.dataSource = new MatTableDataSource(this.stk_movimientos); //Actualizo la tabla

                            if (this.stk_movimientos.length != 0) {
                                this.form.get('deposito').disable();
                            }

                            this.alert.success("Artículo añadido con éxito")
                        }
                    },
                });
        } else {
            this.alert.warning("Seleccione un Depósito")
        }
    }

    /**
     * El resultado de esta operación es un nuevo 
     * array que contiene todos los elementos de la lista original, 
     * excepto el artículo que se desea eliminar.
     * @param articulo 
     */
    deleteArticulo(articulo) {
        this.stk_movimientos = this.stk_movimientos.filter((item) => { return item !== articulo });
        this.dataSource = new MatTableDataSource(this.stk_movimientos);

        if (this.stk_movimientos.length === 0) {
            this.form.get('deposito').enable();
        }

        this.alert.success("Artículo eliminado con éxito")
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input: string) {
        this.form.get(input).setValue('');
    }

    newForm() {
        const [dia, mes, anio] = this.stock_inicial ? this.stock_inicial.fecha.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        if (this.stock_inicial) { //Sí existe el stock inicial, cargo la grilla con sus articulos...
            this.getMovimientos(this.stock_inicial.idstockinicial).then((res) => {
                // Mapeo la cantidad
                res.map(m => m.cantidad = parseFloat(m.cantidad));

                this.stk_movimientos = res; // Le asigno los movimientos
                this.dataSource = new MatTableDataSource(res);
                if (this.stk_movimientos.length != 0) {
                    this.form.get('deposito').disable();
                } else {
                    this.form.get('deposito').enable();
                }
            });
        }

        this.form = new FormGroup({
            user: new FormControl((this.user ? this.user.name : '')),
            fecha: new FormControl((this.stock_inicial?.fecha ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            hora: new FormControl((this.stock_inicial?.hora ? this.stock_inicial.hora : this.datePipe.transform(new Date(), 'HH:mm')), [Validators.required]),
            deposito: new FormControl((this.stock_inicial ? this.stock_inicial.descripcion_deposito : ''), Validators.required),
            articulo: new FormControl((''),),
            unidadmedida: new FormControl((''),),
            cantidad: new FormControl((''),),
            observaciones: new FormControl((this.stock_inicial ? this.stock_inicial.observaciones : ''), [Validators.maxLength(this.maxPalabras)])
        });
    }

    onSubmit() {
        // Verificar la longitud de las observaciones
        if (!this.isPalabraMaxLengthReached()) {
            return this.alert.error(`La longitud máxima de observaciones es de ${this.maxPalabras} caracteres.`);
        }

        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            let fh = this.datePipe.transform(this.form.get('fecha').value, 'yyyy-MM-dd'); //Esta linea hace que siempre se mantenga el formato de la facha cuando se modifica manualmente.
            let articulos = this.stk_movimientos.map(objeto => ({   //Encapsulo solamente los datos que utilizaré de los articulos...
                idarticulo: objeto.idarticulo,
                cantidad: Number(objeto.cantidad),
                idunidadmedida: objeto.idunidadmedida
            }))

            if (articulos.length === 0) {
                return this.alert.warning('Debe ingresar un articulo al stock inicial')
            }

            const payload = {
                idusuario: this.user.id,
                fecha_hora: `${fh} ${this.form.get('hora').value}`,
                hora: this.form.get('hora').value,
                iddeposito: this.form.get('deposito').value.iddeposito,
                observaciones: this.form.get('observaciones').value,
                articulos: articulos
            }

            if (this.stock_inicial?.idstockinicial) {
                let depo = this.depositos.find(d => d.iddeposito === this.stock_inicial.iddeposito); //Hago esto porque cuando es una edicion me levanta el texto del deposito y, sino lo cambio durante la edicion, al persistr pincha porque le estoy enviando un string en vez de un number en 'iddeposito'.
                payload['iddeposito'] = depo.iddeposito;

                this._stockInicialService.updateStockInicial(payload, this.stock_inicial.idstockinicial)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            } else {
                this._stockInicialService.saveStockInicial(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.dialogRef.close(data);
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}